import { ThreeElements } from '@react-three/fiber';
import React, { useEffect, useRef } from 'react';
import { BufferAttribute, DoubleSide, Mesh, PlaneGeometry } from 'three';

const Surface = (props: ThreeElements['mesh']) => {
	const meshRef = useRef<Mesh>(null!);
	const ref = useRef<PlaneGeometry>(null!);

	const size = 100;
	const gridSize = 100;
	const stepSize = 1;
	const amplitude = 10;
	const wireframe = false;

	// Function to calculate the y-coordinate for each vertex
	const calculateY = (x: number, z: number): number => {
		const frequency = 0.1;
		return Math.sin(x * frequency) * Math.cos(z * frequency) * amplitude;
	};

	// Create the grid of vertices
	const vertices: number[] = [];
	const colors: number[] = [];
	for (let x = -gridSize / 2; x <= gridSize / 2; x += stepSize) {
		for (let z = -gridSize / 2; z <= gridSize / 2; z += stepSize) {
			const y = calculateY(x, z);
			vertices.push(x, y, z);
		}
	}

	useEffect(() => {
		ref.current.setAttribute('position', new BufferAttribute(new Float32Array(vertices), 3));
		ref.current.setAttribute('color', new BufferAttribute(new Float32Array(colors), 3));
	}, []);

	return (
		<mesh {...props} ref={meshRef}>
			<planeGeometry ref={ref} attach="geometry" args={[size, size, gridSize, gridSize]} />
			<meshPhongMaterial
				attach="material"
				// vertexColors={true}
				color={'darkgreen'}
				side={DoubleSide}
				wireframe={wireframe}
				flatShading={true}
			/>
		</mesh>
	);
};

export default Surface;
