import React from 'react';
import { Canvas } from '@react-three/fiber';
import Surface from '../meshes/Surface';
import { OrbitControls, Stats, Sky } from '@react-three/drei';

const Screen = () => {
	return (
		<div className="canvas-container">
			<Canvas camera={{ position: [30, 10, 30] }}>
				<ambientLight intensity={Math.PI / 2} />
				<pointLight position={[10, 20, -10]} decay={0} intensity={Math.PI} />
				<Surface position={[0, 0, 0]} scale={0.5} />
				<OrbitControls target={[0, 0, 0]} />
				{/* <Stats className="stats" /> */}
				<Sky />
			</Canvas>
		</div>
	);
};

export default Screen;
